import React from 'react';
import {
  BrowserRouter as Router,
  Switch,
  Route
} from 'react-router-dom';
import Events from './Events';
import Header from './Component/Header';
import Cards from './Component/Cards';
import Main from './Component/Main';
import LaunchPatient from './Component/Launch/LaunchPatient';
import LaunchProvider from './Component/Launch/LaunchProvider';
import './App.css';

export default class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      Loading: false,
      Oauth2: null,
      Cerner: null,
      MainArray: ["Observation", "Condition", "MedicationStatement", "AllergyIntolerance", "Patient", "Person", "RelatedPerson", "CarePlan"],
      MainIndex: 0,
      DisplayCount: 25,
      DisplayIndex: 0
    }
    this.SetAppState = this.SetAppState.bind(this);
  }

  SetAppState(event) {
    this.setState(event);
  }

  componentDidMount() {
    const { Cerner } = this.state;
    if (!Cerner) {
      Events.client.ready({
        SetAppState: this.SetAppState
      });
      this.setState({ Loading: true });
    }
  }

  render() {
    const {
      Loading,
      Oauth2,
      Cerner,
      MainArray,
      MainIndex,
      DisplayCount,
      DisplayIndex
    } = this.state;

    if (Cerner) console.log("Cerner:", Cerner);

    return <div className="App">
      <Router basename="/UMX-DX">
        {Loading ? null : (
          <Header 
            Oauth2={Oauth2}
            SetAppState={this.SetAppState}
          />
        )}
        <Switch>
          <Route exact path='/'>
            {Loading ? (
              <div className="App-Loading">
                <h1>Please wait...</h1>
              </div>
            ) : (
              !Oauth2 ? (
                <div className="App-Login">
                  <h1>Please select account type to Log-In</h1>
                </div>
              ) : (
                <Cards 
                  CardArray={MainArray}
                  Oauth2={Oauth2}
                  SetAppState={this.SetAppState}
                />
              )
            )}
          </Route>
              {Oauth2 ? (
                <>
                  <Route path="/observation">
                    {Loading ? (
                      <div className="App-Loading">
                        <h1>Please wait...</h1>
                      </div>
                    ) : (
                      <Main 
                        Oauth2={Oauth2}
                        Cerner={Cerner}
                        MainArray={MainArray}
                        MainIndex={MainIndex}
                        DisplayCount={DisplayCount}
                        DisplayIndex={DisplayIndex}
                        SetAppState={this.SetAppState}
                      />
                    )}
                  </Route>
                  <Route path="/condition">
                    {Loading ? (
                      <div className="App-Loading">
                        <h1>Please wait...</h1>
                      </div>
                    ) : (
                      <Main 
                        Oauth2={Oauth2}
                        Cerner={Cerner}
                        MainArray={MainArray}
                        MainIndex={MainIndex}
                        DisplayCount={DisplayCount}
                        DisplayIndex={DisplayIndex}
                        SetAppState={this.SetAppState}
                      />
                    )}
                  </Route>
                  <Route path="/medication-statement">
                    {Loading ? (
                      <div className="App-Loading">
                        <h1>Please wait...</h1>
                      </div>
                    ) : (
                      <Main 
                        Oauth2={Oauth2}
                        Cerner={Cerner}
                        MainArray={MainArray}
                        MainIndex={MainIndex}
                        DisplayCount={DisplayCount}
                        DisplayIndex={DisplayIndex}
                        SetAppState={this.SetAppState}
                      />
                    )}
                  </Route>
                  <Route path="/allergy-intolerance">
                    {Loading ? (
                      <div className="App-Loading">
                        <h1>Please wait...</h1>
                      </div>
                    ) : (
                      <Main 
                        Oauth2={Oauth2}
                        Cerner={Cerner}
                        MainArray={MainArray}
                        MainIndex={MainIndex}
                        DisplayCount={DisplayCount}
                        DisplayIndex={DisplayIndex}
                        SetAppState={this.SetAppState}
                      />
                    )}
                  </Route>
                  <Route path="/patient">
                    {Loading ? (
                      <div className="App-Loading">
                        <h1>Please wait...</h1>
                      </div>
                    ) : (
                      <Main 
                        Oauth2={Oauth2}
                        Cerner={Cerner}
                        MainArray={MainArray}
                        MainIndex={MainIndex}
                        DisplayCount={DisplayCount}
                        DisplayIndex={DisplayIndex}
                        SetAppState={this.SetAppState}
                      />
                    )}
                  </Route>
                  <Route path="/person">
                    {Loading ? (
                      <div className="App-Loading">
                        <h1>Please wait...</h1>
                      </div>
                    ) : (
                      <Main 
                        Oauth2={Oauth2}
                        Cerner={Cerner}
                        MainArray={MainArray}
                        MainIndex={MainIndex}
                        DisplayCount={DisplayCount}
                        DisplayIndex={DisplayIndex}
                        SetAppState={this.SetAppState}
                      />
                    )}
                  </Route>
                  <Route path="/related-person">
                    {Loading ? (
                      <div className="App-Loading">
                        <h1>Please wait...</h1>
                      </div>
                    ) : (
                      <Main 
                        Oauth2={Oauth2}
                        Cerner={Cerner}
                        MainArray={MainArray}
                        MainIndex={MainIndex}
                        DisplayCount={DisplayCount}
                        DisplayIndex={DisplayIndex}
                        SetAppState={this.SetAppState}
                      />
                    )}
                  </Route>
                  <Route path="/care-plan">
                    {Loading ? (
                      <div className="App-Loading">
                        <h1>Please wait...</h1>
                      </div>
                    ) : (
                      <Main 
                        Oauth2={Oauth2}
                        Cerner={Cerner}
                        MainArray={MainArray}
                        MainIndex={MainIndex}
                        DisplayCount={DisplayCount}
                        DisplayIndex={DisplayIndex}
                        SetAppState={this.SetAppState}
                      />
                    )}
                  </Route>
                </>
              ) : (
                <>
                  <Route path="/launch-patient">
                    <LaunchPatient />
                  </Route>
                  <Route path="/launch-provider">
                    <LaunchProvider />
                  </Route> 
                </>
              )}
        </Switch>
      </Router>
    </div>
  }
};