import React from 'react';
import Events from '../Events';
import Table from './Table';

export default class Main extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      CurrentPage: 1
    }
    this.SetMainState = this.SetMainState.bind(this);
  }

  SetMainState(event) {
    this.setState(event);
  }

  componentDidMount() {
    const { Oauth2, Cerner, MainArray, MainIndex, SetAppState } = this.props;
    if (Cerner) {
      Events.client.routeChange({
        Oauth2: Oauth2,
        Cerner: Cerner,
        Page: MainArray[MainIndex],
        Value: MainIndex,
        SetAppState: SetAppState,
        SetMainState: this.SetMainState
      });
    }
  }

  render() {
    const {
      Cerner,
      DisplayCount,
      DisplayIndex,
      SetAppState
    } = this.props, {
      CurrentPage
    } = this.state;

    return <div className="App-Main">
      <ul className="Subheader-Nav">
        <li 
          className="Subheader-Nav-Link"
          onClick={() => SetAppState({ Cerner: null })}
        >Back</li>
        <li className="Subheader-Nav-Link">
          <label className="Subheader-Label">
            Count:
            <select 
              className="Subheader-Select"
              value={DisplayCount} 
              onChange={event => {
                SetAppState({ DisplayCount: parseInt(event.target.value), DisplayIndex: 0 });
                this.SetMainState({ CurrentPage: 1 });
              }}
            >
              <option value={25}>25</option>
              <option value={50}>50</option>
              <option value={100}>100</option>
            </select>
          </label>
        </li>
      </ul>
      <Table
        Cerner={Cerner}
        DisplayCount={DisplayCount}
        DisplayIndex={DisplayIndex}
        CurrentPage={CurrentPage}
        SetAppState={SetAppState}
        SetMainState={this.SetMainState}
      />
    </div>
  }
}