import React from 'react';
import { NavLink } from 'react-router-dom';

export default class Header extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            dropdown: false
        }
    }

    render() {
        const {
            Oauth2,
            SetAppState
        } = this.props, {
            dropdown
        } = this.state;

        return <header className="App-Header">
            <NavLink 
                to="/" 
                className="Header-Link"
                onClick={() => SetAppState({ Cerner: null })}
            >UMX-DX App</NavLink>
            {Oauth2 ? (
                <div className="Header-Navbar">
                    <button
                        className="Header-Navbar-Button"
                        onClick={() => this.setState(prevState => ({ dropdown: !prevState.dropdown }))}
                    >Menu</button>
                    {dropdown ? (
                        <div className="Header-Navbar-List">
                            <NavLink 
                                to="/observation" 
                                className="Header-Navbar-Link"
                                activeClassName="Header-Navbar-Link-Active"
                                onClick={() => this.setState(prevState => ({ dropdown: !prevState.dropdown }))}
                            >Observations</NavLink>
                            <NavLink 
                                to="/condition" 
                                className="Header-Navbar-Link"
                                activeClassName="Header-Navbar-Link-Active"
                                onClick={() => this.setState(prevState => ({ dropdown: !prevState.dropdown }))}
                            >Conditions</NavLink>
                            <NavLink 
                                to="/medication-statement" 
                                className="Header-Navbar-Link"
                                activeClassName="Header-Navbar-Link-Active"
                                onClick={() => this.setState(prevState => ({ dropdown: !prevState.dropdown }))}
                            >Medications</NavLink>
                            <NavLink 
                                to="/allergy-intolerance" 
                                className="Header-Navbar-Link"
                                activeClassName="Header-Navbar-Link-Active"
                                onClick={() => this.setState(prevState => ({ dropdown: !prevState.dropdown }))}
                            >Allergies</NavLink>
                            <NavLink 
                                to="/patient" 
                                className="Header-Navbar-Link"
                                activeClassName="Header-Navbar-Link-Active"
                                onClick={() => this.setState(prevState => ({ dropdown: !prevState.dropdown }))}
                            >Patient</NavLink>
                            <NavLink 
                                to="/person" 
                                className="Header-Navbar-Link"
                                activeClassName="Header-Navbar-Link-Active"
                                onClick={() => this.setState(prevState => ({ dropdown: !prevState.dropdown }))}
                            >Person</NavLink>
                            <NavLink 
                                to="/related-person" 
                                className="Header-Navbar-Link"
                                activeClassName="Header-Navbar-Link-Active"
                                onClick={() => this.setState(prevState => ({ dropdown: !prevState.dropdown }))}
                            >Relatives</NavLink>
                            <NavLink 
                                to="/care-plan" 
                                className="Header-Navbar-Link"
                                activeClassName="Header-Navbar-Link-Active"
                                onClick={() => this.setState(prevState => ({ dropdown: !prevState.dropdown }))}
                            >Care Plan</NavLink>
                            <NavLink 
                                to="/" 
                                className="Header-Navbar-Link"
                                onClick={() => {
                                    this.setState(prevState => ({ dropdown: !prevState.dropdown }))
                                    SetAppState({
                                        Oauth2: null,
                                        Cerner: null,
                                        MainIndex: 0,
                                        DisplayCount: 25,
                                        DisplayIndex: 0
                                    });
                                }}
                            >Log Out</NavLink>
                        </div>
                    ) : null }
                </div>
            ) : (
                <div className="Header-Navbar">
                    <button
                        className="Header-Navbar-Button"
                        onClick={() => this.setState(prevState => ({ dropdown: !prevState.dropdown }))}
                    >Launch</button>
                    {dropdown ? (
                        <div className="Header-Navbar-List">
                            <NavLink 
                                to="/launch-patient" 
                                className="Header-Navbar-Link"
                                onClick={() => this.setState(prevState => ({ dropdown: !prevState.dropdown }))}
                            >Patient</NavLink>
                            <NavLink 
                                to="/launch-provider" 
                                className="Header-Navbar-Link"
                                onClick={() => this.setState(prevState => ({ dropdown: !prevState.dropdown }))}
                            >Provider</NavLink>
                        </div>
                    ) : null }
                </div>
            )}
        </header>
    }
};